@import url(https://fonts.googleapis.com/css?family=Dosis);
* {
	box-sizing: border-box;
	color: rgb(0, 0, 0);
	font-family: 'Dosis', sans-serif;
	font-size: 16pt;
	font-weight: 300;
	border: 0;
}

body {
	padding: 0;
	margin: 0;
}
.app-container {
	display: grid;
	height: 100vh;
	width: 100vw;
	grid-template-columns:  1fr 3fr 1fr;
	grid-template-rows: 1fr auto auto auto 4rem 1fr;
	grid-row-gap: 0.5rem;
	grid-template-areas:
		". . ."
		". title ."
		". username-input ."
		". password-input ."
		". login-btn ."
		". . .";
	margin: 0;
	padding: 1rem;
	background: rgb(171, 169, 169);
}
.to-home-btn {
	grid-area: home-btn;
	display: flex;
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
}

 .fa-arrow-left {
	font-size: 2.5rem;
	color:rgb(255, 255, 255);
	text-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
}
.kitchen-service-container {
	display: grid;
	height: 100vh;
	width: 100vw;
	padding: 1rem;
	background-color: rgb(171, 169, 169);
	grid-template-columns: 1fr;
	grid-template-rows: 3rem auto;
	grid-template-areas:
		"kitchen-display"
		"orders-display"
	;
}

.orders-display {
	grid-area: orders-display;
	display: block;
}

.kitchen-display {
	grid-area: kitchen-display;
	display: grid;
	border-radius: 0.25rem;
	grid-template-columns: 1fr 4fr 4fr 2fr 4fr 2fr;
	grid-column-gap: 0.5rem;
	grid-template-rows: auto;
	grid-template-areas:
	"home-btn display-hour display-waiter display-table display-preparing display-deliver";

}

.home-btn {
	grid-area: home-btn;
	display: flex;
	align-items: center;
	justify-content: center;

}
.display-waiter {
	grid-area: display-waiter;
	display: flex;
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	padding: 0.5rem;
	color: rgb(247, 244, 244);
	background: rgb(185, 185, 185);
}

.display-hour {
	grid-area: display-hour;
	display: flex;
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	padding: 0.5rem;
	color: rgb(247, 244, 244);
	background: rgb(185, 185, 185);
}

.display-table {
	grid-area: display-table;
	display: flex;
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	padding: 0.5rem;
	color: rgb(247, 244, 244);
	background: rgb(185, 185, 185);

}

.display-order {
	grid-area: display-order;
	display: flex;
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	padding: 0.5rem;
	color: rgb(247, 244, 244);
	background: rgb(185, 185, 185);

}

.display-preparing {
	grid-area: display-preparing;
	display: flex;
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	color: rgb(247, 244, 244);
	background: rgb(185, 185, 185);

}

.display-deliver {
	grid-area: display-deliver;
	display: flex;
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	color: rgb(247, 244, 244);
	background: rgb(185, 185, 185);
}


.single-order-container {
	display: grid;
	padding: 1rem;
	border-radius: 0.25rem;
	grid-template-columns: 1fr 4fr 4fr 2fr 4fr 2fr;
	grid-column-gap: 0.5rem;
	grid-template-rows: auto auto;
	grid-template-areas:
	"order-delete order-date order-waiter order-table order-preparing order-deliver"
	". items notes notes  . ."
	;
}

.order-delete, .order-date, .order-waiter, .order-table, .items, .order-preparing, .order-deliver {
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 0.25rem;
	padding: 0.5rem;
	color: whitesmoke;

}

.order-delete {
	grid-area: order-delete;
}

.order-date {
	grid-area: order-date;
	background:rgb(200, 200, 200);
}

.order-waiter {
	grid-area: order-waiter;
	background:rgb(200, 200, 200);

}

.order-table {
	grid-area: order-table;
	background:rgb(200, 200, 200);

}

.items {
	grid-area: items;
	display: block;
}

.notes {
	grid-area: notes;
	margin: 0.5rem;
}

.order-preparing, .order-preparing-true {
	grid-area: order-preparing;
	box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
}

.order-preparing {
	background-color: rgb(200, 200, 200);
}

.order-preparing-true {
	background-color: rgb(219, 202, 126);
	border-radius: 0.25rem;
}

.order-deliver {
	grid-area: order-deliver;
	background-color: rgb(200, 200, 200);
	box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
}

.order-ready-true {
	grid-area: order-deliver;
	background-color: rgb(146, 202, 123);
	border-radius: 0.25rem;
}

i {
	color: whitesmoke;
	text-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
}

.ordered-item {
	display: block;
	grid-area: ordered-item;
	padding: 0.5rem;
	cursor: pointer;
	border-radius: 0.25rem;
	background: rgb(183, 218, 162);
	margin-bottom: 0.5rem;
}
.added-note{
	font-size: 12pt;
	border-radius: 0.25rem;
	background-color: rgb(186, 186, 186);
	padding: 0.5rem;
	margin: 0.25rem;
	align-items: center;
}
.select-service-container {
	display: grid;
	height: 100vh;
	width: 100vw;
	grid-template-columns:  1fr 30vw 1rem 30vw 1fr;
	grid-template-rows: 1fr 4rem 1fr;
	grid-template-areas:
		". . . . ."
		". kitchen-btn . service-btn ."
		". . . . .";
	margin: 0;
	background: rgb(171, 169, 169);
}

.kitchen-btn {
	grid-area: kitchen-btn;
	display: flex;
	background: rgb(183, 218, 162);
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.4);
}

.service-btn {
	grid-area: service-btn;
	display: flex;
	background: rgb(183, 218, 162);
	border-radius: 0.25rem;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.4);
}
.select-table-container {
	display: grid;
	width: 100vw;
	height: 100vh;
	grid-template-columns: 0.5rem 1fr 1fr 1fr 2fr 0.5rem;
	grid-template-rows: 1rem 4rem auto auto auto 1rem;
	grid-row-gap: 0.5rem;
	grid-template-areas:
		". . . . . ."
		". home-btn tables-title tables-title table-id ."
		". show-tables show-tables show-tables . ."
		". show-tables show-tables show-tables . ."
		". show-tables show-tables show-tables . ."
		". . . . . .";
		background: rgb(171, 169, 169);

}

.tables-title {
	grid-area: tables-title;
	display: flex;
	font-size: 2rem;
	font-weight: 900;
	color: white;
	text-align: center;
	align-items: center;
	justify-content: flex-center;
}

.selected-table {
	grid-area: selected-table;
	display: flex;
	border-radius: 0.25rem;
	background-color: rgb(230, 224, 165);
	align-items: center;
	justify-content: center;
}

.show-tables {
	grid-area: show-tables;
	display: flex;
	flex-wrap: wrap;
	background: rgb(171, 169, 169);
	align-items: center;
	justify-content: center;
}

.table-btn, .table-btn-preparing, .table-btn-ready {
	display: flex;
	padding: 1.5rem;
	margin: 1rem;
	border-radius: 0.25rem;
	box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
}

.table-btn {
	background-color: rgb(151, 176, 201);
}

.table-btn-preparing {
	background-color: rgb(219, 202, 126);
}

.table-btn-ready {
	background-color: rgb(152, 219, 126);
}
.ready-btn {

	display: flex;
	grid-area: ready-btn;
	align-items: center;
	justify-content: center;

}

i {
	color: whitesmoke;
	text-shadow: rgba(0, 0, 0, 0.6) 0px 0px 3px;
}
.table-id {
	display: flex;
	grid-area: table-id;
	border-radius: 0.25rem;
	background-color: rgb(230, 224, 165);
	align-items: center;
	justify-content: center;
}
.check-container {
	grid-area: check-container;
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 4rem 0.5rem 1fr;
	grid-template-areas:
		"table-id"
		"."
		"table-order";
	padding: 0.5rem;
	background-color: rgb(194, 194, 194);
	border-radius: 0.25rem;
	overflow: hidden;
}
.table-order {
	grid-area: table-order;
	display: grid;
	grid-template-rows: auto 0.5rem 3rem 0.5rem 2rem 1rem 4rem;
	grid-template-areas:
		"show-ordered-item"
		"."
		"added-notes"
		"."
		"total-check"
		"."
		"send-order";
	overflow: hidden;
}

.show-ordered-item {
	grid-area: show-ordered-item;
	padding-right: 0.5rem;
	overflow-y: scroll;
}

.added-notes{
	grid-area: added-notes;
	display: block;
	overflow-y: scroll;
}
.total-check {
	display: flex;
	grid-area: total-check;
	border-radius: 0.25rem;
	background-color: rgb(171, 171, 171);
	padding: 0.5rem;
	margin: 0.25rem;
	align-items: center;
}
.send-order {
	display: flex;
	grid-area: send-order;
	background-color: rgb(218, 162, 172);
	border-radius: 0.25rem;
	font-weight: bold;
	align-items: center;
	justify-content: center;
}

.select-menu-container {
	display: grid;
	grid-area: select-menu-container;
	grid-template-columns: 1fr;
	grid-template-rows: 4rem 0.5rem 1fr 0.5rem auto;
	grid-template-areas:
		"meal-type"
		"."
		"menu-items"
		"."
		"dashboard";
	background-color: rgb(194, 194, 194);
	border-radius: 0.25rem;
	padding: 0.5rem;
}

.menu-items {
	grid-area: menu-items;
	display: block;
}

.menu-items {
	display: block;
	grid-area: menu-items;

}
.item {
	display: block;
	padding: 0.5rem;
	cursor: pointer;
	border-radius: 0.25rem;
	background: rgb(183, 218, 162);
	margin-bottom: 0.5rem;
}
.meal-type {
	grid-area: meal-type;
	display: grid;
	grid-template-columns: 1fr 0.5rem 1fr 0.5rem 1fr;
	grid-template-areas:
		'breakfast-button . meals-button . beverages-button';
}
.breakfast {
	grid-area: breakfast-button;
	display: flex;
	background: rgb(162, 215, 218);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
	justify-content: center;

}

.meals{
	grid-area: meals-button;
	display: flex;
	background: rgb(173, 162, 218);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
    justify-content: center;
}

.beverages {
	grid-area: beverages-button;
	display: flex;
	background: rgb(218, 162, 216);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
	justify-content: center;

}
.dashboard {
    grid-area: dashboard;
    display: grid;
    grid-template-columns: 1fr 1rem 1fr;
    grid-template-rows: 3rem 1rem 4rem;
    grid-template-areas:
    "notes notes notes"
    ". . ."
    "home-button . send-note";
}
.notes {
	display: block;
	grid-area: notes;
	padding: 0.25rem;
	border-radius: 0.25rem;
	font-size: 12pt;
	outline: none;
	overflow: hidden;
	overflow-wrap: break-word;
	overflow-y: scroll;
	resize: none;
	text-overflow: clip;
	background-color: rgb(186, 186, 186);
}
.home-button {
    grid-area: home-button;
    display: flex;
	background: rgb(218,162,192);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
    justify-content: center;
}
.send-note {
    grid-area: send-note;
    display: flex;
	background: rgb(162, 178, 218);
	border-radius: 0.25rem;
	border: 0;
	align-items: center;
    justify-content: center;
}
main {
	display: grid;
	height: 100vh;
	width: 100vw;
	grid-template-columns: 1rem 2fr 1rem 1.5fr 1rem;
	grid-template-rows: 1rem 1fr 1rem;
	grid-template-areas:
		". . . . ."
		". select-menu-container . check-container ."
		". . . . .";
	background: rgb(171, 169, 169);
}
